.chats-block__chat {
  height: 66px;
  background-color: #ededed;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  display: flex;
}

.chats-block__chat:hover {
  cursor: pointer;
  background-color: #fff;
  border-top: 1px solid silver;
  border-bottom: 1px solid silver;
}

.chats-block__chat_active {
  background-color: #a5d4ee;
  border-top: 1px solid #a5d4ee;
  border-bottom: 1px solid #a5d4ee;
}

.chats-block__chat_active:hover {
  border-top: 1px solid #5fbbef;
  border-bottom: 1px solid #5fbbef;
}

.chats-block__chat:first-child:hover {
  border-top: 1px solid #fff;
}

.chats-block__chat-avatar {
  width: 52px;
  height: 52px;
  background-color: silver;
  border-radius: 50%;
  margin: 8px;
}

.chats-block__chat-information {
  width: calc(100% - 68px);
  flex-direction: column;
  justify-content: space-around;
  padding: 5px 10px 5px 5px;
  display: flex;
}

.chats-block__chat-information-top {
  justify-content: space-between;
  display: flex;
}

.chats-block__chat-title {
  max-height: 16.8px;
  font-size: 14px;
  overflow: hidden;
}

.chats-block__chat-time {
  color: #4a4a4a;
  margin-left: 8px;
  font-size: 12px;
}

.chats-block__chat-information-bottom {
  justify-content: space-between;
  display: flex;
}

.chats-block__chat-message {
  max-height: 16.8px;
  color: #4a4a4a;
  overflow: hidden;
}

.chats-block__chat-amount {
  height: 14px;
  text-align: center;
  color: #fff;
  background-color: #5fbbef;
  border-radius: 7px;
  margin-left: 8px;
  padding: 1.4% 2.6%;
  font-size: 10px;
  font-weight: 700;
  line-height: 10px;
}

@font-face {
  font-family: Exo2;
  src: url("Exo2-Light.4540d3c3.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Exo2;
  src: url("Exo2-Bold.08bc5033.ttf");
  font-weight: 700;
}

@font-face {
  font-family: Exo2;
  src: url("Exo2-ExtraBold.61c07a93.ttf");
  font-weight: 800;
}

.main {
  width: 100%;
  height: 100%;
  place-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.input {
  height: 36px;
  background-color: #ededed;
  border: none;
  border-radius: 5px;
  padding: 0 10px;
  font-family: Exo2;
  font-size: 18px;
  font-weight: 300;
  line-height: 36px;
}

.button {
  cursor: pointer;
}

a {
  color: #717171;
  text-decoration: none;
}

a:visited {
  color: #717171;
}

